img#dance-girl {
  position: absolute;
  width: 100%;
  z-index: -1;
}

.hero {
  font-family: Kalam;

  img#no-gotcha-no-life {
    position: relative;
    z-index: -2;
    width: 80%;
    display: block;
    margin: auto;
  }

  p.hero-text {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 10rem;

    @media (max-width: 768px) {
      font-size: 1.4rem;
      margin-bottom: 5rem;
    }
  }

  margin-bottom: 5rem;
}

.presale-table {
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  font-size: 2rem;
  border-collapse: collapse;
  font-family: "Kalam";

  th {
    background-color: #bf5562;
  }

  th,
  td {
    border-bottom: 1px dashed #ddd;
    padding: 8px;
  }

  tr:last-child {

    th,
    td {
      border: none;
    }
  }

  @media (max-width: 991px) {
    font-size: 1.6rem;
  }

  @media (max-width: 768px) {
    font-size: 1.3rem;
  }

  @media (max-width: 576px) {
    padding-left: 1rem;
    font-size: 1rem;
  }
}

.presale-comp {
  text-transform: uppercase;
  text-align: center;
  font-family: Wendy one;
  padding: 3rem 0;

  .rc-slider {
    margin-bottom: 2rem;

    @media (max-width: 768px) {
      margin-bottom: 1.6rem;
    }
  }

  .rc-slider-handle {
    &::after {
      content: '';
      display: block;
      width: 60px;
      height: 60px;
      background-image: url(../assets/images/logo.png);
      background-repeat: no-repeat;
      background-size: contain;
      position: absolute;
      top: -90%;
      left: 50%;
      transform: translate(-50%, -50%);

      @media (max-width: 768px) {
        width: 40px;
        height: 40px;
        top: -60%;
      }
    }
  }

  .deposit-btn {
    font-size: 1.6rem;
    @media (max-width: 991px) {
      font-size: 1.4rem;
    }
    @media (max-width: 768px) {
      font-size: 1.2rem;
    }
    @media (max-width: 450px) {
      font-size: 1rem;
    }
  }

  p.sold {
    color: #ffb0cb;
    text-transform: uppercase;
    text-align: left;
    font-size: 2rem;
    margin-bottom: 5rem;

    span {
      font-size: 2.5rem;
    }

    @media (max-width: 768px) {
      font-size: 1.6rem;

      span {
        font-size: 2rem;
      }
    }
  }

  .input-token-panel {
    display: inline-block;
    background-color: transparent;
    text-align: left;
    
    .input-token-box {
      display: flex;
      border: solid 1px white;
      border-radius: 8px;
      justify-content: space-between;
      margin-right: 2rem;
      font-size: 2rem;
      @media (max-width: 991px) {
        font-size: 1.4rem;
      }
      @media (max-width: 768px) {
        margin-right: 0;
        margin-bottom: 1.5rem;
        font-size: 1.25rem;
      }
      
      .input-token {
        background: transparent;
        border:none;
        outline: none;
        padding: 10px;
        font-size: 2rem;
        font-weight: bold;
        color: white;
        white-space: nowrap;
        text-overflow: ellipsis;
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        -webkit-align-items: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
          -webkit-appearance: none;
          margin: 0;
        }
        @media (max-width: 991px) {
          width: 60%;
          font-size: 1.4rem;
        }
        @media (max-width: 768px) {
          width: 70%;
          font-size: 1.25rem;
        }
      }
      
      .btn-max {
        background-color: transparent;
        border: none;
        color: white;
      }
      
      .cro-box {
        display: flex;
        align-items: center;
        padding: 0.3rem;
        .image-cro {
          position: static;
          margin-right: 0.6rem;
          width: 2rem;
          height: 2rem;
          justify-content: center;
        }
      }
    }
  
    .input-token-balance {
      display: flex;
      justify-content: space-between;
      font-size: 1.6rem;
      @media (max-width: 768px) {
        font-size: 1.2rem;
      }
    }
  }
  
  .setter {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 3rem;
    column-gap: 2rem;
    width: 400px;
    margin: auto;
    margin-bottom: 3rem;
    justify-items: center;

    @media (max-width: 768px) {
      row-gap: 2rem;
      column-gap: 0rem;
      width: 300px;
    }

    @media (max-width: 450px) {
      width: 100%;
    }

    button {
      border: none;
      text-transform: uppercase;
      font-size: 1.5rem;
      text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      width: 9rem;

      &#public-sale {
        background-color: #376f69;
        color: #bbbab9;
        border: 1px solid #bbbab9;
      }

      &#claim {
        background-color: #1d1c1b;
        color: #bbbab9;
        border: 1px solid #bbbab9;

        &:hover {
          background-color: #8f9695;
        }

        &:active {
          background-color: #21867a;
        }
      }

      &#plus,
      &#minus {
        background-color: transparent;
        color: #bf5562;
        font-size: 3rem;
      }

      @media (max-width: 768px) {
        font-size: 1.2rem;
        width: 6rem;
      }
    }

    &.claim {
      .amount {
        grid-column: span 2;
      }
    }
  }

  p.amount {
    font-size: 3rem;
    margin-bottom: 0;
    line-height: 4rem;

    span {
      font-size: 4rem;

      @media (max-width: 768px) {
        font-size: 3rem;
      }

      @media (max-width: 450px) {
        font-size: 2rem;
      }
    }

    @media (max-width: 768px) {
      font-size: 2rem;
    }

    @media (max-width: 768px) {
      font-size: 1.6rem;
    }
  }

  p.note {
    font-size: 1.6rem;

    @media (max-width: 768px) {
      font-size: 1.2rem;
    }

    @media (max-width: 768px) {
      font-size: 1rem;
    }
  }

  .round-button {
    background-color: #3c0d0c;
    color: #bbbab9;

    &:hover {
      background-color: #bb0909;
    }
    &.disabled, &[disabled], &:disabled {
      background-color: #767676;
      &:hover {
        background-color: #525252;
      }
    }
    &:disabled {
      background-color: #767676;
      opacity: 1;
    }
  }

  .total-raised {
    margin-bottom: 2rem;
    font-size: 2rem;
    
    @media (max-width: 768px) {
      font-size: 1.5rem;
    }
  }
}
